
@import '@/assets/scss/variables.scss';

.container {
  flex-grow: 1;
  margin: 0 auto;
  padding: 0 40px;
  font-family: 'Poppins', sans-serif;
}

body {
  overflow: hidden;
}

.cart {
  display: grid;
  grid-template-columns: 4fr 1fr;
  grid-template-rows: repeat(2, fit-content);
  column-gap: 1rem;
  position: relative;
  user-select: none;

  &__crumbs {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
  }

  &__info {
    padding: 1rem;
    background-color: $white;
    border-radius: 10px;
    @include apply-shadow;
  }

  .products {
    display: grid;
    flex-direction: column;
    align-self: center;
    gap: 0.5rem;
    position: relative;
    padding-left: 0;
  }
}

.empty {
  &__info {
    color: $secondary;
  }
}

.products-anime {
  &-move,
  &-enter-active,
  &-leave-active {
    transition: all 0.3s ease;
  }

  &-enter-from,
  &-leave-to {
    opacity: 0;
    transform: scale(0.9);
  }

  &-leave-active {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}
