
.product-list {
  width: 100%;

  &_not-found {
    min-height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    font-weight: 700;
  }

  &_type_grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    row-gap: 1.5rem;
    column-gap: 1rem;
    place-items: center center;
  }

  &_type_list {
    display: flex;
    flex-direction: column;
  }

  &__item {
    margin-top: 15px;

    &_type_list {
      margin: 20px;
    }
  }
}

.loading {
  width: 100%;
  height: 100px;
  display: block;
}
