
@import '@/assets/scss/variables.scss';

.product__info {
  flex-basis: 50%;
  margin-top: 1rem;
  font-weight: 200;

  &--header {
    display: flex;
    flex-direction: column;

    .header__category {
      @include text-style;
    }

    .header {
      &__category--title,
      &__brand--title {
        color: $secondary;
        font-style: italic;
      }
    }
  }

  &--description {
    margin-top: 1rem;

    .description {
      &__text--title {
        color: $secondary;
        font-style: italic;
      }

      &__text {
        margin: 0;
        @include text-style;
      }
    }
  }

  &--return {
    display: flex;
    align-items: center;
    gap: 0.5em;
    margin: 1rem 0 0.5rem 0;
    font-size: 0.9rem;

    .fa-arrows-rotate {
      color: $warning;
    }
  }

  &--sale {
    font-weight: 600;
    color: $success;
  }
}
