
@import '@/assets/scss/variables.scss';

.app-input {
  display: flex;
  flex-direction: column;

  .form {
    &__header {
      margin: 0;
      font-size: 0.9em;
      text-transform: uppercase;
    }

    &__group {
      display: flex;
      flex-direction: column;
      position: relative;

      &--input {
        font-family: 'Poppins', sans-serif;
        flex-basis: 100%;
        padding: 0.3em 0.6rem;
        font-size: 1em;
        border: none {
          radius: 10px;
        }
        outline: 2px solid $secondary;
        transition: all 0.2s;

        &:focus-visible {
          transform: scale(0.96);
          outline-color: $primary;
        }

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
      }

      &--check {
        position: absolute;
        right: -1.4em;
        top: 2px;

        .fa-circle-check,
        .fa-circle-xmark {
          background-color: $light;
          border-radius: 50%;
        }

        .fa-circle-check {
          color: $success;
        }

        .fa-circle-xmark {
          color: $danger;
        }
      }
    }
  }

  &__alert {
    opacity: 1;
    margin-top: 0.3rem;
    font-size: 0.8rem;
    color: $danger;
  }
}
