
@import '@/assets/scss/variables.scss';

$count-color: $warning;
$total-color: $success-light;

@keyframes ping {
  0% {
    translate: 44% -33%;
  }

  50% {
    translate: 44% -60%;
  }

  100% {
    translate: 44% -33%;
  }
}

.cart-info {
  position: relative;
  margin-top: 5px;
  width: 55px;
  height: 55px;
  color: $black;
  font-size: 1rem;

  &__link {
    transition: background-color 0.5s;
    background-color: $primary2;
    border-radius: 50%;
    @include apply-shadow;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 1.6rem;
    scale: 1;
    transition: scale 0.2s, background-color 0.2s;
  }

  &__count {
    color: $black;
    background-color: $count-color;
    padding: 0 10px;
    border-radius: 100px;
    translate: 44% -33%;
    right: 0;
    top: 0;
    position: absolute;
    transition: translate 0.5s;

    &_ping {
      animation: ping 0.3s cubic-bezier(0.46,-0.42, 0.63, 1.57);
    }
  }

  &__total {
    color: $white;
    background-color: $total-color;
    padding: 0 10px;
    border-radius: 100px;
    translate: -50% 30%;
    rotate: 10deg;
    transition: scale 0.2s, rotate 0.2s;
    bottom: 0;
    left: 0;
    position: absolute;
    pointer-events: none;
  }

  &:hover {
    .cart-info__link {
      background-color: $primary2-light;
    }

    .cart-info__total {
      rotate: 370deg;
      scale: 1.2;
    }
  }

  &:active {
    .cart-info__link {
      scale: 0.9;
    }
  }
}
