
@import '@/assets/scss/variables.scss';

.card {
  position: relative;
  padding: 0.5rem 1.8rem;
  min-width: 365px;
  background: {
    image: url(@/assets/img/cardBG.jpg);
    size: cover;
    repeat: no-repeat;
  }
  border-radius: 10px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0;
  }

  &__bank-name {
    margin: 0;
  }

  &__pay-service {
    &--img {
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      max-width: 70px;
    }
  }

  &__chip {
    margin-left: 1rem;

    &--img {
      display: block;
      max-width: 50px;
    }
  }

  &__number {
    :deep(.form__group--input) {
      text-align: center;
    }
  }

  &__info {
    width: 100%;

    &--owner {
      font-size: 0.8em;
      font-weight: 700;
    }

    &--inputs {
      display: flex;
      gap: 1rem;

      .date {
        flex-basis: 50%;
      }

      .cvv {
        flex-basis: 50%;
      }

      :deep(.app-input .form) {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.5em;
        font-size: 0.8em;

        .form__header {
          font-weight: 400;
        }

        .form__group--input {
          text-align: center;
          max-width: 5em;
        }
      }
    }
  }
}
