
@import '@/assets/scss/variables.scss';

.container {
  margin: 0 auto;
  flex-grow: 1;
  padding: 0 40px;
  font-family: 'Poppins', sans-serif;
}

.product {
  display: flex;
  flex-direction: column;
  user-select: none;

  &__content {
    display: flex;
    justify-content: center;
    gap: 1rem;
    position: relative;

    .content__wrapper {
      flex-basis: 80%;
      display: flex;
      gap: 1rem;
      padding: 1rem;
      font-family: 'Nunito', sans-serif;
      border-radius: 10px;
      background-color: $white;
      @include apply-shadow;
    }
  }
}
