
@import '@/assets/scss/variables.scss';

.product__price {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;

  &--full {
    font-weight: 600;
    font-size: 1.2rem;
  }

  &--discount {
    font-weight: 600;
    color: $success;
  }

  &--final {
    font-size: 1.2rem;
    font-weight: 600;
  }

  .crossed {
    text-decoration: line-through;
    font-weight: 400;
    font-size: 1rem;
    font-style: italic;
    color: $secondary;
  }
}
