
@import '@/assets/scss/variables.scss';

.summary__total-price {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: repeat(2, 1fr);
  width: 100%;
  font-size: 1.3rem;

  &--fixed,
  &--total {
    justify-self: end;
  }

  &--fixed {
    grid-column: 2 / 3;
  }

  .crossed {
    font-size: 1.1rem;
    color: $danger;
    text-decoration: line-through;
  }
}
