
@import '@/assets/scss/variables.scss';

.summary__promo-list {
  padding: 0.5rem;
  width: 100%;
  @include block-style;

  &--header {
    margin: 0;
    padding-bottom: 0.5rem;
  }

  &--code {
    display: grid;
    grid-template-columns: 2.5fr 2fr 0.3fr;
    align-items: center;
    justify-content: space-between;
    padding: 0.3rem 0;
    border-top: 1px solid $secondary;

    .promo__value {
      color: $success;
    }
    .promo__delete {
      justify-self: end;
      padding: 0.1rem 0.5rem;
      transition: all 0.2s;

      &:hover {
        color: $danger;
        cursor: pointer;
      }
    }
  }
}
