
@import '@/assets/scss/variables.scss';

.summary-form {
  .summary {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: sticky;
    top: 80px;
    padding: 1rem;
    background-color: $white;
    border-radius: 12px;
    @include apply-shadow;

    &__buy {
      margin-top: 1rem;
      width: 100%;
    }
  }
}
