

@import '@/assets/scss/variables.scss';

.app-body {
  background-color: $light;
  font-family: 'Poppins', sans-serif;
  position: relative;
}

.app-body__header {
  padding: 0;
  margin: 0;
  width: 100%;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
}

.app-body__footer {
  padding: 0;
  margin: 0;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.app-main {
  min-height: 100vh;
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

