

$inner-padding: 10px;
$thumb-width: 20px;
$bar-width: 4px;
$fill-bar-width: 6px;

@import "@/assets/scss/variables.scss";

.double-slider {
  margin: 10px calc(10px + $inner-padding);
  padding: $inner-padding 0; 
  position: relative;
  cursor: grab;

  &__bar {
    pointer-events: none;
    border-radius: 2px;
    border: $gray-200 calc($bar-width / 2) solid;
  }

  &__fill-bar {
    pointer-events: none;
    top: calc($inner-padding + $bar-width / 2 -  $fill-bar-width / 2);
    position: absolute;
    border-radius: 2px;
    border: $white calc($fill-bar-width / 2) solid;
  }

  &__thumb {
    pointer-events: none;
    width: $thumb-width;
    height: $thumb-width;
    position: absolute;
    top: calc($inner-padding + $bar-width / 2 -  $thumb-width / 2);
    background-color: $white;
    border: $primary 3px solid;
    border-radius: 50%;
    translate: -50% 0;
  }
}

