
@import '@/assets/scss/variables.scss';

.product__header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 1rem;

  &--title {
    margin: 0.5rem;
    color: $primary-dark;
    @include text-style;
  }

  &--info {
    display: flex;
    gap: 1rem;
    color: $secondary;

    .gold {
      color: $warning;
    }

    .header__info {
      &--rating {
        color: $warning;
      }

      &--feedback {
        border-bottom: 1px dashed $secondary;
        transition: all 0.2s;
        cursor: pointer;

        &:hover {
          color: $dark;
          border-color: $dark;
        }
      }
    }
  }
}
