
@import '@/assets/scss/variables.scss';

.product__count-info {
  justify-self: start;
  display: flex;
  align-items: center;

  .delete {
    width: 100%;
    padding: 1rem;
    background-color: transparent;
    border: none;
    cursor: pointer;
    opacity: 0;
    transition: all 0.3s;

    &:hover {
      color: $danger;
      transform: scale(1.2);
    }
    .fa-trash {
      font-size: 1rem;
    }
  }

  .active {
    opacity: 1;
  }

  .count-info__wrapper {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;

    &--control {
      padding: 0.3rem;
      background-color: $white;
      border-radius: 12px;
      @include block-style;

      .increment,
      .decrement {
        padding: 0.5rem;
        background-color: transparent;
        border: none {
          radius: 10px;
        }
        transition: all 0.2s;
        cursor: pointer;

        &:hover {
          color: $light;
          background-color: $primary-dark;
        }
        &:hover:disabled {
          background-color: transparent;
        }
      }
      .count {
        font-size: 1rem;
        max-width: 3rem;
        background-color: transparent;
        border: none;
        text-align: center;
        -webkit-appearance: none;
        appearance: none;

        &:focus-visible {
          outline: none;
        }

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
      }
    }

    &--single-price,
    &--stock {
      color: $secondary;
    }
  }
}
