
@import '@/assets/scss/variables.scss';

.product__crumbs {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  font-family: 'Nunito', sans-serif;
  color: $secondary;

  .fa-arrow-left {
    position: relative;
    margin-right: 0.5em;
    padding: 0.5rem;
    font-size: 1.2rem;
    color: $dark;
    transition: all 0.2s;
    cursor: pointer;

    &:hover {
      color: $primary;
    }
  }

  .crumbs__list {
    display: flex;
    gap: 0.5rem;
    padding: 0;
    list-style: none;

    .crumbs {
      border-bottom: 1px dashed transparent;
      cursor: pointer;
      transition: all 0.2s;

      &::first-letter {
        text-transform: uppercase;
      }

      &:hover {
        color: $dark;
        border-bottom: 1px dashed $dark;

        &::before {
          color: $secondary;
        }
      }
    }

    .crumbs:not(:last-child) {
      &::after {
        content: '/';
        margin-left: 0.5rem;
        font-size: 0.9em;
        color: $secondary;
      }
    }
  }
}
