

@import '@/assets/scss/variables.scss';

.page-footer {
  background-color: $primary2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  box-shadow: 0px -3px 4px rgba(0, 0, 0, 0.25);
}

.course-link {
  display: block;
  &__logo {
    display: block;
    width: 90px;
  }
}

.year {
  font-size: 1.2rem;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  align-self: center;
  text-align: center;
}

.authors-section {
  display: flex;
  align-items: center;

  &__heading {
    font-size: 1.5rem;
    margin: 0;
    font-family: 'Pacifico', cursive;
    font-weight: 300;
  }
}

