
@import '@/assets/scss/variables.scss';

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 35px;
  background-color: $primary;
  color: $white;

  @include apply-shadow;
}

.logo {
  user-select: none;
  font-size: 2rem;
  font-family: 'Pacifico', cursive;
  transition: scale 0.5s;
  @include logo-style;

  &:hover {
    scale: 1.2;
  }
}
