
@import '@/assets/scss/variables.scss';

.product__info {
  justify-self: start;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding-left: 1rem;
  text-align: left;

  &--header {
    display: flex;
    align-items: center;

    .fa-star {
      color: $warning;
    }

    .header {
      &__rating {
        margin-left: 0.2rem;
        font-weight: 600;
        color: $warning;
      }

      &__title {
        margin-left: 1rem;
        font-weight: 600;
        color: $primary-dark;
        border-bottom: 1px solid transparent;
        transition: all 0.2s;
        @include text-style;

        &:hover {
          color: $primary-dark;
          border-bottom: 1px solid $primary-dark;
        }
      }
    }
  }

  &--brand {
    color: $secondary;
  }

  &--description {
    line-height: 1.3em;
    @include text-style;
  }
}
