
@import '@/assets/scss/variables.scss';

.product-count {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  color: $gray-600;
}

.view-options {
  &__btn {
    font-size: 2.5rem;
    transition: color 0.25s;

    &_active {
      color: $primary;
    }

    &:hover {
      color: $primary;
      transform: scale(1.05);
    }
  }
}

.product-section {
  width: 100%;
  padding: 10px 40px;
  padding-right: 0;
  display: flex;
  flex-direction: column;

  &__heading {
    font-family: 'Pacifico', cursive;
    font-size: 2rem;
    font-weight: normal;
    margin: 0;
    margin-bottom: 10px;
    user-select: none;
  }

  &__options {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

