
@import '@/assets/scss/variables.scss';

.not-found {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  &__status {
    width: 150px;
    height: 150px;
  }

  &__heading {
    font-size: 3rem;
    user-select: none;
    font-weight: 500;
    margin: 0;
    margin-bottom: 10px;
  }

  &__desc {
    user-select: none;
    font-weight: 300;
    font-size: 1.5rem;
    margin: 0;
  }
}
