
@import '@/assets/scss/variables.scss';

.payment {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__buy-group {
    display: flex;
    flex-direction: column;

    .payment__sum {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      margin: 1rem 0;
      font-size: 1.1rem;
      text-transform: uppercase;

      &--header {
        font-weight: 600;
      }

      &--total {
        color: $success;
      }
    }

    &--alert {
      margin-top: 0.3rem;
      font-size: 0.8rem;
      color: $danger;
      opacity: 0;
    }

    .visible {
      opacity: 1;
    }
  }
}
