
@import '@/assets/scss/variables.scss';

#thumbnails {
  margin-top: 1rem;
}

.product__img {
  flex-basis: 50%;
  flex-grow: 0;
  width: 400px;
  padding: 1rem;
  border-radius: 10px;
  background-color: $light;

  .slider__img {
    &,
    &--main {
      display: block;
      width: 100%;
      border-radius: 10px;
      cursor: pointer;
    }

    & {
      max-height: 100px;
      border: 1px solid $secondary;
    }

    &--main {
      max-width: 400px;
      max-height: 300px;
    }
  }
}
