

@import '@/assets/scss/variables.scss';

.filter-categories-list {
  height: 100%;

  &__item {
    margin: 8px 0;
  }
}

.category {
  display: flex;
  justify-content: space-between;

  &__name {
    flex-grow: 1;
    max-width: 60%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__count {
    width: 40px;
    text-align: right;
  }
}

