
@import './common.scss';
.product-list-item {
  display: flex;
  justify-content: space-between;

  &__image-wrap {
    padding: 20px;
    display: flex;
    align-items: center;
  }

  &__heading {
    font-size: 1.2rem;
    white-space: normal;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    width: 100%;
  }

  &__head {
    display: flex;
  }

  &__actions {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  &__price {
    font-size: 1.9rem !important;
  }

  &__price-value {
    margin: 0 !important;
    display: block;
  }
}
